<template>
	<div id="statcenter-order-detail">
		<public-header title="统计详情"></public-header>
		<div class="content">
			<div class="text-center">
				<van-row class="font-14 c-gray flex-lr">
					<van-col span="6">日期</van-col>
					<van-col span="9">总收入</van-col>
					<van-col span="9">配送成功</van-col>
				</van-row>
				<van-row class="font-14 flex-lr" v-for="(item, index) in records" :key="index">
					<van-col span="6">{{item.stat_day}}</van-col>
					<van-col span="9" class="c-primary">+¥{{item.total_fee}}</van-col>
					<van-col span="9" class="c-danger">{{item.total_success_order}}</van-col>
				</van-row>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				days: 0,
				stat_day: {
					start: '',
					end: ''
				},
				records: [],
				showPreLoading: true
			}
		},
		methods: {
			onLoad() {
				var that = this;
				var query = that.util.parseQuery(that.$route.query);
				that.days = query.days;
				that.type = query.type;
				that.stat_day.start = query.start;
				that.stat_day.end = query.end;
				getData({
					vue: that,
					url: 'delivery/statcenter/stat/detail',
					data: {
						type: that.type,
						days: that.days,
						stat_day: that.stat_day
					},
					success: (res) => {
						that.records = res.records;
					}
				})
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#statcenter-order-detail .van-row:nth-child(2n+1){
		background-color: #fff;
	}
	#statcenter-order-detail .van-row:nth-child(2n){
		background-color: #eee;
	}
	#statcenter-order-detail .van-row:first-child{
		background-color: #eee;
		border-bottom: 1px solid #fefefe;
	}
	#statcenter-order-detail .van-row .van-col{
		font-size: 13px;
		padding: 5px 0;
		min-height: 36px;
		line-height: 1.2;
		display: flex;
		align-items: center;
		justify-content: center;
		border-right: 1px solid #fefefe;
	}
	#statcenter-order-detail .van-row:first-child .van-col{
		min-height: 36px;
	}
	#statcenter-order-detail .van-row .van-col:last-child{
		border: 0px;
	}
</style>


